#footer {  
    width: 100%;
    position: relative;
    z-index: 1;
    user-select: none;
    border-top: 1px solid #eee;
    .row1 {
        width: 100%;
        background: #fff;
        .container{
            width: 100%;
            max-width: 1050px;
            margin: 0 auto;
            .row1-row1{
                width: 100%;
                display: flex;
                padding: 30px 0;
                .left{
                    width: 45%;
                    ul{
                        li{
                            &:nth-child(1){
                                padding: 0 0 30px 0 ;
                                h2{
                                    font-size: 20px;
                                    color: #333;
                                    font-weight: 600;
                                }
                            }
                            &:nth-child(2){
                                padding: 0 0 30px 0 ;
                                h3{
                                    a{
                                        font-size: 28px;
                                        color: #333;
                                        font-weight: 700;
                                    }
                                    span{
                                        font-size: 16px;
                                        color: #333;
                                        margin: 0 0 0 10px;
                                        vertical-align: bottom;
                                    }
                                }
                            }
                            &:nth-child(3){
                                p{
                                    padding: 0 0 15px 0 ;
                                    a{
                                        display: inline-flex;
                                        justify-content: center;
                                        align-items: center;
                                        width: 140px;
                                        height: 40px;
                                        border: 1px solid #ccc;
                                        font-size: 14px;
                                        color: #333;
                                        border-radius: 2px;
                                    }
                                    span{
                                        margin: 0 0 0 15px;
                                        display: inline-flex;
                                        justify-content: left;
                                        font-size: 13px;
                                        color: #999;
                                        line-height: 130%;
                                    }
                                }
                            }
                            &:nth-child(4){
                                p{
                                    font-size: 12px;
                                    em{
                                        color: #999;
                                    }
                                    a{
                                        color: #999;
                                    }
                                }
                            }
                        }
                    }
                }
                .right{
                    width: 55%;
                    display: flex;
                    justify-content: flex-end;
                    ul{
                        li{
                            &:nth-child(1){
                                font-size: 14px;
                                a{
                                    color: #444;
                                    margin: 0 15px 0 0;
                                }
                            }
                            &:nth-child(2){
                                padding: 30px 0;
                                p{
                                    font-size: 12px;
                                    color: #999;
                                    line-height: 150%;
                                    i{
                                        color: #ccc;
                                        margin: 0 10px;
                                        font-size: 11px;
                                        vertical-align: top;
                                    }
                                    a{
                                        color: #5f0080;
                                    }
                                }
                            }
                            &:nth-child(3){
                                display: flex;
                                a{
                                    margin: 0 12px 0 0;
                                    img{
                                        width: 30px;
                                        height: 30px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .row1-row2{
                border-top: 1px solid #eee;
                width: 100%;
                padding: 30px 0;
                ul{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    li{

                        a{
                            display: flex;
                            img{
                                width: auto;
                                height: 34px;
                            }
                            span{
                                font-size: 10px;
                                line-height: 140%;
                                color: #999;
                                margin: 0 0 0 10px;    
                            }
                        }
                        &:nth-child(3){
                            a{
                                img{
                                    height: 32px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .row2 {
        width: 100%;
        background: #f7f7f7;
        padding: 30px 0 40px 0;
        .container{
            width: 100%;
            max-width: 1050px;
            margin: 0 auto;
            text-align: center;
            p{
                color: #999;
                font-size: 12px;
                line-height: 150%;
                &:last-child{
                    padding: 10px 0 0 0;
                }
            }
        }
    }
}